import React from "react";
import classes from "./Logo.module.css";

const Logo = ({ style, type }) => {
	const cls = [type === "header" ? classes.header : null, classes.wrapper];
	return (
		<div className={cls.join(" ")} style={{ ...style }}>
			<span className={classes.firstPart}>Jack</span>
			<span className={classes.secondPart}>Movies</span>
		</div>
	);
};

export default Logo;
