import React from "react";
import classes from "./Footer.module.css";
import Logo from "../../components/Hero/Logo/Logo";

const Footer = () => {
	const left = ["About", "Terms of Service", "Contact"];
	const right = [
		"fa fa-facebook",
		"fa fa-twitter",
		"fa fa-pinterest",
		"fa fa-instagram",
		"fa fa-youtube",
	];

	return (
		<div className={classes.footer}>
			<div className={classes.left}>
				{left.map((link, id) => {
					return (
						<p key={id} className={classes.link}>
							{link}
						</p>
					);
				})}
			</div>
			<div className={classes.middle}>
				<Logo style={{ color: "#0aaee4", fontSize: 40 }} />
				<span className={classes.copyright}>
					Copyright  2020 <Logo style={{ color: "#000", fontSize: 16 }} />. All
					Rights Reserved.
				</span>
			</div>
			<div className={classes.right}>
				{right.map((link, id) => {
					return <i key={id} className={link + " " + classes.icon}></i>;
				})}
			</div>
		</div>
	);
};

export default Footer;
