import React from "react";
import classes from "./Movie.module.css";
import useWindowDimensions from "../../hooks/useWindowDimensions";
import { NavLink } from "react-router-dom";
import Rating from "../../components/UI/Rating/Rating";
import MainButton from "../UI/MainButton/MainButton";

const Movie = ({ data, id }) => {
	let movie = data.find((movie) => movie.id === +id);
	const { height } = useWindowDimensions();
	let body;

	if (movie) {
		let hours = Math.floor(movie.length / 60);
		let minutes = movie.length % 60;
		body = (
			<div
				className={classes.background}
				style={{
					backgroundImage: `url('${movie.bigImage}')`,
					height: height - height * 0.1,
				}}
			>
				<div className={classes.position}>
					<p className={classes.name}>{movie.name}</p>
					<div className={classes.wrapper}>
						<div className={classes.info}>
							<Rating score={movie.rating} style={{ marginRight: 10 }} />

							<NavLink to="/year" className={classes.link}>
								{movie.year}
							</NavLink>

							<span className={classes.genres}>
								{movie.genres.map((genre, id) => {
									return (
										<span>
											<span key={id} className={classes.genre}>
												{genre}
											</span>
											{id !== movie.genres.length - 1 ? ", " : null}
										</span>
									);
								})}
							</span>

							<span className={classes.length}>
								{hours > 0 ? `${hours}h` : null} {minutes}m
							</span>

							<NavLink to="/country" className={classes.link}>
								{movie.country}
							</NavLink>

							<span className={classes.ageRestriction}>
								{movie.ageRestriction}+
							</span>
						</div>

						<div className={classes.description}>{movie.description}</div>

						<div className={classes.info}>
							<MainButton text="Watch Now" form="blue" />
						</div>
					</div>
				</div>
			</div>
		);
	} else {
		body = (
			<div className={classes.notFound}>
				<h1>Sorry. We could find the movie you were looking for.</h1>
				<h1>But we have a lot of other movies!</h1>
				<NavLink to="/">Go home</NavLink>
			</div>
		);
	}

	return <div>{body}</div>;
	// <div
	// 	className={classes.background}
	// 	style={{
	// 		backgroundImage: `url('${movie.bigImage}')`,
	// 		height: height - height * 0.1,
	// 	}}
	// >
	// 	<div className={classes.position}>
	// 		<p className={classes.name}>{movie.name}</p>
	// 		<div className={classes.wrapper}>
	// 			<div className={classes.info}>
	// 				<Rating score={movie.rating} style={{ marginRight: 10 }} />

	// 				<NavLink to="/year" className={classes.link}>
	// 					{movie.year}
	// 				</NavLink>

	// 				<span className={classes.genres}>
	// 					{movie.genres.map((genre, id) => {
	// 						return (
	// 							<span>
	// 								<span key={id} className={classes.genre}>
	// 									{genre}
	// 								</span>
	// 								{id !== movie.genres.length - 1 ? ", " : null}
	// 							</span>
	// 						);
	// 					})}
	// 				</span>

	// 				<span className={classes.length}>
	// 					{hours > 0 ? `${hours}h` : null} {minutes}m
	// 				</span>

	// 				<NavLink to="/country" className={classes.link}>
	// 					{movie.country}
	// 				</NavLink>

	// 				<span className={classes.ageRestriction}>
	// 					{movie.ageRestriction}+
	// 				</span>
	// 			</div>

	// 			<div className={classes.description}>{movie.description}</div>

	// 			<div className={classes.info}>
	// 				<MainButton text="Watch Now" form="blue" />
	// 			</div>
	// 		</div>
	// 	</div>
	// </div>
};

export default Movie;
